import { parseLoc, parseTarget } from "../PhotoGuide/parse";

export function getHelpTexts({ target, pos, cms }) {
  switch (pos) {
    case 0: {
      return [cms.Kamera_Texte.Koerper.Text_1, cms.Kamera_Texte.Koerper.Text_2];
    }
    case 1: {
      // Fessel
      return [
        cms.Kamera_Texte.Fessel.Text_1.replace("{target}", parseLoc(target)),
        cms.Kamera_Texte.Fessel.Text_2.replace("{target}", parseLoc(target)),
        cms.Kamera_Texte.Fessel.Text_3.replace("{target}", parseLoc(target)),
        cms.Kamera_Texte.Fessel.Text_4.replace("{target}", parseLoc(target)),
        cms.Kamera_Texte.Fessel.Text_5.replace("{target}", parseLoc(target)),
      ];
    }
    case 2: {
      // Hufbreite
      return [
        cms.Kamera_Texte.Huf.Text_1.replace("{target}", parseLoc(target)).replace("{mode}", "breitesten"),
        cms.Kamera_Texte.Huf.Text_2.replace("{target}", parseLoc(target)).replace("{mode}", "breitesten"),
        cms.Kamera_Texte.Huf.Text_3.replace("{target}", parseLoc(target)).replace("{mode}", "breitesten"),
        cms.Kamera_Texte.Huf.Text_4.replace("{target}", parseLoc(target)).replace("{mode}", "breitesten"),
        cms.Kamera_Texte.Huf.Text_5.replace("{target}", parseLoc(target)).replace("{mode}", "breitesten"),
        cms.Kamera_Texte.Huf.Text_6.replace("{target}", parseLoc(target)).replace("{mode}", "breitesten"),
        cms.Kamera_Texte.Huf.Text_7.replace("{target}", parseLoc(target)).replace("{mode}", "breitesten"),
        cms.Kamera_Texte.Huf.Text_8.replace("{target}", parseLoc(target)).replace("{mode}", "breitesten"),
      ];
    }
    case 3: {
      // Huflänge
      return [
        cms.Kamera_Texte.Huf.Text_1.replace("{target}", parseLoc(target)).replace("{mode}", "längsten"),
        cms.Kamera_Texte.Huf.Text_2.replace("{target}", parseLoc(target)).replace("{mode}", "längsten"),
        cms.Kamera_Texte.Huf.Text_3.replace("{target}", parseLoc(target)).replace("{mode}", "längsten"),
        cms.Kamera_Texte.Huf.Text_4.replace("{target}", parseLoc(target)).replace("{mode}", "längsten"),
        cms.Kamera_Texte.Huf.Text_5.replace("{target}", parseLoc(target)).replace("{mode}", "längsten"),
        cms.Kamera_Texte.Huf.Text_6.replace("{target}", parseLoc(target)).replace("{mode}", "längsten"),
        cms.Kamera_Texte.Huf.Text_7.replace("{target}", parseLoc(target)).replace("{mode}", "längsten"),
        cms.Kamera_Texte.Huf.Text_8.replace("{target}", parseLoc(target)).replace("{mode}", "längsten"),
      ];
    }
  }
}

export function getHelpPageNumber(pos) {
  switch (pos) {
    case 0:
      return 2;
    case 1:
      return 5;
    case 2:
      return 7;
    case 3:
      return 7;
  }
}

export function getHelpImages({ target, pos, cms }) {
  switch (pos) {
    case 0: {
      // Fessel
      return [
        cms.Kamera_Texte.Koerper.Bild_1.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Koerper.Bild_2.localFile.childImageSharp.gatsbyImageData,
      ];
    }
    case 1: {
      // Fessel
      return [
        cms.Thumbnails[parseTarget(target)].localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Fessel.Bild_1.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Fessel.Bild_2.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Fessel.Bild_3.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Fessel.Bild_4.localFile.childImageSharp.gatsbyImageData,
      ];
    }
    case 2: {
      // Hufbreite
      return [
        cms.Thumbnails[parseTarget(target)].localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_Breite.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_1.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_2.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_3.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_4.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_5.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_6.localFile.childImageSharp.gatsbyImageData,
      ];
    }
    case 3: {
      // Hufbreite
      return [
        cms.Thumbnails[parseTarget(target)].localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_Laenge.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_1.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_2.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_3.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_4.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_5.localFile.childImageSharp.gatsbyImageData,
        cms.Kamera_Texte.Huf.Bild_6.localFile.childImageSharp.gatsbyImageData,
      ];
    }
  }
}
